* {
  box-sizing: border-box;
}

.body {
  flex-direction: row;
  justify-content: center;
  display: flex;
  position: relative;
}

.form-container {
  max-width: 80%;
  background-color: #2196f3;
  margin: auto;
}

.search-form {
  max-width: 300px;
  margin: auto;
}

.input {
  float: left;
  width: 80%;
  background: #fff;
  border: 1px solid gray;
  padding: 10px;
  font-size: 17px;
}

.formBtn {
  float: left;
  width: 20%;
  color: #fff;
  cursor: pointer;
  background: #f1f1f1;
  border: 1px solid gray;
  border-left: none;
  padding: 10px;
  font-size: 17px;
}

.formBtn:hover {
  background: #0b7dda;
}

.search-form:after {
  content: "";
  clear: both;
  display: table;
}

.load-more {
  position: fixed;
  bottom: 0;
  right: 50%;
}

.load-more:hover {
  color: #fff;
  background-color: #0b7dda;
}

.loadMoreHidden {
  display: none;
}

.loadMoreVisible {
  display: block;
}

.gallery {
  max-width: 80%;
  flex-wrap: wrap;
  justify-content: center;
  margin: 1% auto auto;
  display: flex;
}

.photo-card:not(:last-child) {
  margin-right: 5px;
}

.photo-card {
  width: calc(25% - 22.5px);
}

.photo {
  width: 100%;
  height: 220px;
}

.info {
  max-width: 100%;
  flex-direction: row;
  justify-content: center;
  display: flex;
}

.info-item {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.info-item:not(:last-child) {
  margin-right: 7px;
}

